// Map emoji categories to DB column names
export const CATEGORY_EMOJI_TO_DB_MAP: Record<string, CategoryKey> = {
  'üçî': 'food',
  'üëó': 'clothing',
  'üè†': 'housing',
  'üöó': 'transportation',
  'üìö': 'education',
  'üé≤': 'entertainment',
  'üßª': 'daily',
  'üíä': 'medical',
  'üìâ': 'investment',
  'üìà': 'investment',
  'üí∞': 'salary',
  'üéÅ': 'bonus',
  '‚ú®': 'other',
};

/**
 * Helper: ÂèñÂæóÁâπÂÆöÈ°ûÂà•ÁöÑÂ∏∏Áî®ÊèèËø∞Èô£Âàó
 */
export function getCategoryFavorites(
  favorites: FavoriteCategories | null,
  categoryEmoji: string,
): string[] {
  if (!favorites) return [];

  const dbColumn = CATEGORY_EMOJI_TO_DB_MAP[categoryEmoji];
  if (!dbColumn) return [];

  const value = favorites[dbColumn];
  if (!value || value.trim() === '') return [];

  return value
    .split(',')
    .map((s) => s.trim())
    .filter((s) => s !== '');
}

/**
 * Helper: Êõ¥Êñ∞ÁâπÂÆöÈ°ûÂà•ÁöÑÂ∏∏Áî®ÊèèËø∞
 */
export function updateCategoryFavorites(
  favorites: FavoriteCategories | null,
  categoryEmoji: string,
  descriptions: string[],
): Partial<FavoriteCategories> {
  const dbColumn = CATEGORY_EMOJI_TO_DB_MAP[categoryEmoji];
  if (!dbColumn) {
    throw new Error('ÁÑ°ÊïàÁöÑÈ°ûÂà•');
  }

  return {
    ...favorites,
    [dbColumn]: descriptions.join(','),
  };
}
